import React from 'react'
import styled, {/**css*/} from 'styled-components'
import {AppContext} from '../App/AppProvider'
//import {SelectableTile} from '../shared/Tile'
import CoinTile from './CoinTile'


export const CoinGridStyled = styled.div`
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
	grid-gap: 15px;
	margin-top: 40px;
`

const getLowerSectionCoins = (coinList, filteredCoins) => {
	console.log('From the lower section method :', coinList, filteredCoins)
	return (filteredCoins && Object.keys(filteredCoins)) || Object.keys(coinList).slice(0, 100)
}
const get100Coins = (coinList, topSection, favorites, filteredCoins) => 
	topSection ? favorites : getLowerSectionCoins(coinList, filteredCoins)


export default ({topSection}) => 
	<AppContext.Consumer>
	{
		({coinList, favorites, filteredCoins}) =>
			<CoinGridStyled>
				{
					get100Coins(coinList, topSection, favorites, filteredCoins).map(coinKey => 
						<CoinTile 
							key={coinKey} 
							coinKey={coinKey}
							topSection={topSection}
						>
							{coinKey}
						</CoinTile>
					)
				}
			</CoinGridStyled>
	}
	</AppContext.Consumer>