import React from 'react'
import styled, {css} from 'styled-components'
import {SelectableTile} from '../shared/Tile'
import {fontSize3, fontSizeBig} from '../shared/Styles'
import {CoinHeaderGridStyled} from '../settings/CoinHeaderGrid'
import {AppContext} from '../App/AppProvider'
import {greenBoxShadow} from '../shared/Styles'


const JustifyRight = styled.div`
	justify-self: right;
`

const JustifyLeft = styled.div`
	justify-self: left;
`

const TickerPrice = styled.div`
	${fontSizeBig};
`

const ChangPct = styled.div`
	color: green;
	${
		props => props.red && css`
			color: red;
		`
	}
`

const numberFormat = number =>  +(number + '').slice(0, 7)

const PriceTileStyled = styled(SelectableTile)`
	${
		props => props.compact && css`
		display: grid;
		grid-gap: 5px;
		grid-template-columns: repeat(3, 1fr);
		justify-items: right;
		${fontSize3};
	`}

	${props => props.currentFavorite && css`
		${greenBoxShadow};
		pointer-events: none;
	`}
`

const ChangePercent = ({data}) => 
	<JustifyRight>
		<ChangPct red={data.CHANGEPCT24HOUR < 0}>
			{
				numberFormat(data.CHANGEPCT24HOUR)
			}%
		</ChangPct>
	</JustifyRight>

const PriceTileCompact = ({sym, data, currentFavorite, setCurrentFavorite}) =>
	<PriceTileStyled compact onClick={setCurrentFavorite} currentFavorite={currentFavorite}>
		<JustifyLeft>{sym}</JustifyLeft>
		<ChangePercent data={data} />
		<div>
			${numberFormat(data.PRICE)}
		</div>
	</PriceTileStyled>

const PriceTile = ({sym, data, currentFavorite, setCurrentFavorite}) => 
	<PriceTileStyled onClick={setCurrentFavorite} currentFavorite={currentFavorite}>
		<CoinHeaderGridStyled>
			<div>{sym}</div>
			<ChangePercent data={data} />
		</CoinHeaderGridStyled>
		<TickerPrice>
			${numberFormat(data.PRICE)}
		</TickerPrice>
	</PriceTileStyled>

export default ({price, index}) => {
	let sym = Object.keys(price)[0]
	let data = price[sym]['USD']
	let TileClass = index < 5 ? PriceTile : PriceTileCompact

	return (	
		<AppContext.Consumer>
			{
				({currentFavorite, setCurrentFavorite}) =>
					<TileClass 
						sym={sym} 
						data={data} 
						currentFavorite={currentFavorite === sym}
						setCurrentFavorite={() => setCurrentFavorite(sym)}
					>
					</TileClass>

			}
		</AppContext.Consumer>	
	)
}
